import React from 'react'
import AdminServices from 'services/api/AdminServices';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import cssVar from 'views/mobileView/styles';
import SearchBar from 'views/mobileView/components/atoms/SearchBar';
import db from 'libs/db';

export default function SearchMintboxes(props) {

  const [allMintboxes, setAllMintboxes] = React.useState([]);
  React.useEffect(() => {

    loadData();
  }, []);

  const loadData = async () => {
    getAllMintbox();
  }

  const getAllMintbox = async () => {
    db.get(db.model.user, true).then(async (ap) => {

      let res = await AdminServices.getAllMintboxes(ap.userData.id).then((res) => JSON.stringify(res));
      res = await JSON.parse(res);
      console.log("Get All Mintboxes API: ", res.data)
      if (res.type === true) {
        setAllMintboxes(res.data)
        console.log(allMintboxes)
      }
    })
  }
  const onSearchResultClick = (id) => {
    props.history.push("/mintbox-detail/" + id)
  }


  return (
    <div>
      <MainHeader
        header="Search mintboxes"
        right="help"
        screenType="Invest"
        navigation={props.history}
      />
      <div className="containerData px-0 pb-4 mb-4">
        <SearchBar type='Mintbox' allFunds={allMintboxes} onSearchResultClick={onSearchResultClick} showPlusIcon={false} />
      </div>
    </div>
  )
}
